<template>
  <div class="bacdrop" v-if="show" @click.self="closeModal">
    <div class="modal">
      <div class="status">
        <label>ÿ≠ÿßŸÑ ÿ±Ÿàÿ≤:</label>
        <select v-model="status">
          <option value="üòç">üòç</option>
          <option value="üòë">üòë</option>
          <option value="üò¢">üò¢</option>
        </select>
        <h1>{{ status }}</h1>
        <button @click="closeModal">ÿ´ÿ®ÿ™</button>
      </div>
    </div>
  </div>
</template>

<script>
import { supabase } from '@/supabase'

export default {
  props: ['show'],
  emits: ['close', 'status-submitted'],
  data() {
    return {
      status: 'üòç',
    }
  },
  async mounted() {
    const { data, error } = await supabase.from('status').select('*').limit(1).single()
    if (data) {
      this.status = data.id
    } else if (error) {
      console.error('ÿÆÿ∑ÿß ÿØÿ± ÿÆŸàÿßŸÜÿØŸÜ ÿ≠ÿßŸÑ ÿ±Ÿàÿ≤:', error)
    }
  },
  methods: {
    async closeModal() {
      // ÿ≠ÿ∞ŸÅ ÿ±ÿØ€åŸÅ ŸÇÿ®ŸÑ€å (ÿß⁄Øÿ± ŸÖ€å‚ÄåÿÆŸàÿß€å ŸÅŸÇÿ∑ €å⁄©€å ÿ®ÿßÿ¥Ÿá)
      await supabase.from('status').delete().neq('id', '')

      const { error } = await supabase.from('status').insert([{ id: this.status }])

      if (!error) {
        this.$emit('close')
        this.$emit('status-submitted', this.status)
      } else {
        console.error('ÿÆÿ∑ÿß ÿØÿ± ÿ∞ÿÆ€åÿ±Ÿá ÿ≠ÿßŸÑ ÿ±Ÿàÿ≤:', error.message)
        alert('ÿ´ÿ®ÿ™ ÿ≠ÿßŸÑ ÿ±Ÿàÿ≤ ÿ®ÿß ÿÆÿ∑ÿß ŸÖŸàÿßÿ¨Ÿá ÿ¥ÿØ!')
      }
    },
  },
}
</script>

<style>
.bacdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  width: 400px;
  padding: 30px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.status {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

select {
  background: #f0f4f8;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 8px;
  width: 120px;
}

.modal button {
  background: #4caf50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.modal button:hover {
  background: #45a049;
}
</style>
