<template dir="rtl">
  <div class="madars">
    <form @submit.prevent="handleSubmit">
      <label>ŸÖŸàÿ∂Ÿàÿπ:</label>
      <input v-model="title" type="text" required />

      <label>ÿ¨ÿ≤ÿ¶€åÿßÿ™:</label>
      <textarea v-model="details"></textarea>

      <label>ÿ±Ÿàÿ≤:</label>
      <select v-model="day">
        <option value="one">ÿ±Ÿàÿ≤ ÿßŸàŸÑ</option>
        <option value="two">ÿ±Ÿàÿ≤ ÿØŸàŸÖ</option>
        <option value="three">ÿ±Ÿàÿ≤ ÿ≥ŸàŸÖ</option>
      </select>

      <button>ÿ®ÿ±Ÿàÿ≤ÿ±ÿ≥ÿßŸÜ€å ÿ®ÿ±ŸÜÿßŸÖŸá</button>
    </form>
  </div>
</template>

<script>
import { supabase } from '@/supabase'

export default {
  props: ['id'],
  data() {
    return {
      title: '',
      details: '',
      day: '',
    }
  },
  async mounted() {
    const { data, error } = await supabase
      .from('projects')
      .select()
      .eq('id', this.id)
      .single()

    if (error) {
      console.error('ÿÆÿ∑ÿß ÿØÿ± ÿØÿ±€åÿßŸÅÿ™ ÿßÿ∑ŸÑÿßÿπÿßÿ™ Ÿæÿ±Ÿà⁄òŸá:', error.message)
      alert('ÿÆÿ∑ÿß ÿØÿ± ÿ®ÿßÿ±⁄Øÿ∞ÿßÿ±€å Ÿæÿ±Ÿà⁄òŸá!')
    } else {
      this.title = data.title
      this.details = data.details
      this.day = data.day
    }
  },
  methods: {
    async handleSubmit() {
      const { error } = await supabase
        .from('projects')
        .update({
          title: this.title,
          details: this.details,
          day: this.day,
        })
        .eq('id', this.id)

      if (error) {
        console.error('ÿÆÿ∑ÿß ÿØÿ± ÿ®ÿ±Ÿàÿ≤ÿ±ÿ≥ÿßŸÜ€å Ÿæÿ±Ÿà⁄òŸá:', error.message)
        alert('ÿÆÿ∑ÿß ÿØÿ± ÿ®ÿ±Ÿàÿ≤ÿ±ÿ≥ÿßŸÜ€å Ÿæÿ±Ÿà⁄òŸá! ŸÑÿ∑ŸÅÿßŸã ÿØŸàÿ®ÿßÿ±Ÿá ÿ™ŸÑÿßÿ¥ ⁄©ŸÜ€åÿØ.')
      } else {
        this.$router.push('/')
      }
    },
  },
}
</script>

